import React from 'react';
import Button from '../button/index.web';
import classNames from 'classnames';
export default class Result extends React.Component {
    render() {
        const { prefixCls, className, img, imgUrl, title, message, buttonText, buttonClick, buttonType, style, } = this.props;
        const wrapCls = classNames({
            [`${prefixCls}`]: true,
            [className]: className,
        });
        let imgContent = null;
        if (img) {
            imgContent = <div className={`${prefixCls}-pic`}>{img}</div>;
        }
        else if (imgUrl) {
            imgContent = <div className={`${prefixCls}-pic`} style={{ backgroundImage: `url(${imgUrl})` }}/>;
        }
        return (<div className={wrapCls} style={style} role="alert">
        {imgContent}
        {title ? <div className={`${prefixCls}-title`}>{title}</div> : null}
        {message ? <div className={`${prefixCls}-message`}>{message}</div> : null}
        {buttonText ? <div className={`${prefixCls}-button`}>
          <Button type={buttonType} onClick={buttonClick}>{buttonText}</Button>
        </div> : null}
      </div>);
    }
}
Result.defaultProps = {
    prefixCls: 'am-result',
    buttonType: '',
    buttonClick: () => { },
};



